	module.exports = {"data":{"allDataJson":{"edges":[{"node":{"about":{"presentation":["Figure Figure est une revue exclusivement numérique fondée en 2017 spécialisée dans la jeune création contemporaine. Chaque numéro prend la forme d’un entretien-fleuve avec un artiste émergent. Les publications sont mensuelles et disponibles en libre téléchargement.","Figure Figure est aussi une association loi 1901 fondée en janvier 2019 visant à promouvoir la jeune création en lui donnant la parole, en lui offrant un espace de dialogue, de réflexion et de médiation."]},"contact":[{"title":"Direction de publication","name":"Indira Béraud","email":"Indira@figurefigure.fr"},{"title":"Contribution","name":"Line Ajan","email":"Line@figurefigure.fr"},{"title":"Direction artistique de publication","name":"Fani Morières","email":"Fani@figurefigure.fr"},{"title":"Direction artistique du site web","name":"Thomas Guillemet","email":null},{"title":"Conception du site web","name":"Adrien Béraud","email":null}]}}]}},"pathContext":{}}

